#title {
   text-align: center;
   color: white;
   background-color: rgba(0,0, 0, .5);
   width: 60%;
   border-radius: 25px;
}

#title h1{
  font-size: 350%;
}

#profile {
  text-align: center;
  background-color: rgba(0,0, 0, .5);
  width: 60%;
  border-radius: 25px;
  margin: auto;
  padding: 25px 50px;
}

#profile input{
  border-radius: 25px;
  padding: 10px;
  width: 40%;
}

#profile-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10%;
}

#profile-form{
  color: white;
}

#profile-buttons{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-top:20px;
}

#logout-container {
  text-align: center;
  background-color: rgba(0,0, 0, .5);
  width: 60%;
  border-radius: 25px;
  margin: auto;
  padding: 25px 50px;
}

#logout {
   padding: 15px;
}

#logout {
   text-decoration: none;
   font-weight: lighter;
}

#logout:hover {
   position: relative;
   bottom: 2.5px;
   cursor: pointer;
}
