div#save-outfit {
  display: flex;
}

#outfits {
  list-style: none;
  padding: 7% 6%;
  overflow: scroll;
}

#outfits li:hover {
  cursor: pointer;
  margin-top: -0.8%;
  font-weight: 1000;
  /* font-size: larger; */
}

#outfit-list {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  width: 40%;
  margin-left: 2%;
  margin-top: 2%;
  text-align: center;
  border-radius: 25px;
}

#outfit-list li {
  padding-bottom: 8%;
}

#view-saved {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border-radius: 25px;
  margin-left: 5%;
  margin-right: 2%;
  width: 50%;
  height: 75vh;
  margin-top: 2%;
  overflow: scroll;
}

#outfits button {
  float: right;
  color: red;
  background-color: rgba(0, 0, 0, 0);
}

